float: left;
margin-top: -300px;

;

}
.barra {
float: left;
height: 440px;
width: 300px;
position: absolute;
margin-top: -300px;
background-color:  #0394FF;
transition: all .2s linear;
left: -350px;

}

nav {
width: 100%;
position: absolute;
top: 60px;
}

nav a{
text-decoration: none;
}
.link {
padding: 20px;

background-color:rgb(44, 164, 250);
transition: all .2s linear;
opacity: 0;
margin-top: 200px;
border: solid 1px  rgb(3, 121, 206); 
text-decoration: none;
}

.link:hover {
color: rgb(7, 90, 150);
background-color: rgb(105, 186, 245);
}

#check:checked ~ .barra {
transform: translateX(350px);
}

#check:checked ~ .barra nav a .link{
opacity:1;
margin-top: 0;
transition-delay: .2s;
}




